import Vue from 'vue'
import Router from 'vue-router'
import Content from './views/Content.vue'
import Control from './views/Control.vue'
import Abilities from './views/Abilities.vue'
import Moves from './views/Moves.vue'
import Pokemon from './views/Pokemon.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'content',
      component: Content
    },
    {
      path: '/control',
      name: 'control',
      component: Control
    },
    {
      path: '/abilities',
      name: 'abilities',
      component: Abilities
    },
    {
      path: '/moves',
      name: 'moves',
      component: Moves
    },
    {
      path: '/pokemon',
      name: 'pokemon',
      component: Pokemon
    }
  ]
})